/* GENERAL */

html {
  background-color: hsl(0, 0%, 14%); /*rgb(36, 36, 36);*/
  /*scroll-behavior: smooth;*/
}

.app {
  width: 100%;
}

div {
  user-select: none;
  -moz-user-select: none;
  -o-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
}

/* TABLES */

table {
  width: 80vw;
  margin: 0 10vw;

  table-layout: fixed;
  border-collapse: collapse;

  color: hsl(0, 0%, 96%); /*whitesmoke;*/

  cursor: pointer;
}

.head-table {
  position: sticky; /*fixed;*/
  top: 0px;
  z-index: 2;

  background-color: hsl(0, 0%, 14%);
}

.body-table {
  z-index: 1;
}

.foot-table {
  position: sticky; /*fixed;*/
  bottom: 0px; /* might be higher */
  z-index: 2;

  background-color: hsl(0, 0%, 14%);
}

/* T-HEAD */

thead {
  width: 100%;
  height: 5vw;

  font-size: 1.2vw;
  text-align: center;
}

thead th {
  font-family: 'Rock Salt', cursive;

  overflow: hidden;
}

thead th:first-child {
  width: 24vw; /* 30% */
}

/* Day Cells */

thead th.current:not(.hovered-over) {
  color: hsl(19, 70%, 45%); /*chocolate;*/
}

thead th.hovered-over {
  background-color: hsl(19, 70%, 45%);
  transition: background-color 250ms ease-in-out 20ms;
}

thead th.relative {
  text-decoration: underline;
}

/* First letters */

/* ":first-letter" doesn't work properly in FireFox when whole lines have been removed */

thead th:not(.hovered-over) span:first-child {
  color: hsl(34, 70%, 65%);
}
thead th.free:not(.hovered-over) span:first-child {
  color: hsl(80, 70%, 40%);
}
thead th.done:not(.hovered-over) span:first-child {
  color: hsl(200, 70%, 40%);
}
thead th.super:not(.hovered-over) span:first-child {
  color: hsl(300, 70%, 40%);
}

/* T-BODY */

tbody {
  width: 100%;
}

/* Description Cells */

tbody th {
  width: 24vw; /* 30% */

  line-height: 1.6vw;
  white-space: pre-wrap;

  background-color: hsl(0, 0%, 14%);
}

tbody th, #aux-area {
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  font-size: 1.2vw;
  font-weight: bold;

  overflow: hidden;
}

tbody th.editable, #aux-area.editable {
  color: hsl(39, 100%, 45%); /*orange;*/
  background-color: hsl(0, 0%, 17%); /*rgb(44, 44, 44);*/
}

tbody th.hovered-over, #aux-area.hovered-over {
  color: hsl(0, 0%, 14%);
  background-color: hsl(39, 100%, 45%);
  transition: background-color 250ms ease-in-out 20ms;
}

tbody th.removable, #aux-area.removable {
  color: hsl(0, 0%, 96%);
  background-color: hsl(0, 70%, 45%);
}

tbody th.removable.hovered-over, #aux-area.removable.hovered-over {
  animation: removable-color-pulse 250ms ease-in-out 20ms;
}

@keyframes removable-color-pulse {
  from {
    color: currentColor;
  }
  50% {
    color: hsl(0, 70%, 45%);
  }
  to {
    color: currentColor;
  }
}

tbody th.transforming, #aux-area.transforming {
  color: hsl(0, 0%, 96%);
  background-color: hsl(300, 70%, 45%);
}

tbody th.transforming.hovered-over, #aux-area.transforming.hovered-over {
  animation: transforming-color-pulse 250ms ease-in-out 20ms;
}

@keyframes transforming-color-pulse {
  from {
    color: currentColor;
  }
  50% {
    color: hsl(300, 70%, 45%);
  }
  to {
    color: currentColor;
  }
}

/* can't use 'inherit' */
tbody th.relative, tbody th.relative textarea {
  text-decoration: underline;
}

tbody th>div, #aux-area>div {
  width: 100%;
  height: 8vw; /* V */ /* not aux */

  display: grid;
  grid-template-rows: 1.4vw 1fr 1.4vw;
  grid-template-columns: 1fr 1.4vw;
}

tbody th>div>div, #aux-area>div>div {
  grid-column-start: 1;
  grid-column-end: 2;
  grid-row-start: 1;
  grid-row-end: 4;

  display: flex;
  flex-direction: row;
  -webkit-flex-direction: row;
  justify-content: left;
  align-items: center;
}

/* Diamonds */

tbody th.super>div>div {
  background-size: 10%;
  background-repeat: no-repeat;
  background-position: top left;

  background-image: url('./icons/diamond-pink.svg');
}
tbody th.super.editable>div>div {
  background-image: url('./icons/diamond-orange.svg');
}
tbody th.super.hovered-over>div>div {
  background-image: url('./icons/diamond-dark.svg');
}
tbody th.super:is(.transforming, .removable)>div>div {
  background-image: url('./icons/diamond-light.svg');
}

/* Divs */

tbody th>div>div>div {
  width: 19vw;
  margin-left: 2vw;
  /*margin-right: 2vw;*/

  text-align: left;
}

/* Textareas */

textarea {
  width: 19vw;
  margin-left: 2vw;
  padding: 0px;

  line-height: 1.6vw;

  max-lines: 3;
  
  outline: none;
  -moz-appearance: none;

  resize: none;
  overflow: hidden;

  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;

  color: inherit;
  background-color: inherit;

  /*border: 1px solid currentColor;*/
  border: none;
}

/* seems not to be useful */
textarea.trial-render {
  caret-color: transparent !important;
}

tbody th.editable textarea::selection, #aux-area.editable textarea::selection {
  color: hsl(0, 0%, 17%);
  background-color: hsl(39, 100%, 45%);
}
tbody th.hovered-over textarea::selection, #aux-area.hovered-over textarea::selection {
  color: hsl(39, 100%, 45%);
  background-color: hsl(0, 0%, 14%);
}
tbody th.removable textarea::selection, #aux-area.removable textarea::selection {
  color: hsl(0, 70%, 45%);
  background-color: hsl(0, 0%, 96%);
}
tbody th.transforming textarea::selection, #aux-area.transforming textarea::selection {
  color: hsl(300, 70%, 45%);
  background-color: hsl(0, 0%, 96%);
}

textarea::placeholder {
  opacity: 0.6;
}

tbody th.editable textarea::placeholder, #aux-area.editable textarea::placeholder {
  color: hsl(39, 100%, 45%);
}
tbody th.hovered-over textarea::placeholder, #aux-area.hovered-over textarea::placeholder {
  color: hsl(0, 0%, 14%);
}
tbody th.removable textarea::placeholder, #aux-area.removable textarea::placeholder {
  color: hsl(0, 0%, 96%);
}
tbody th.transforming textarea::placeholder, #aux-area.transforming textarea::placeholder {
  color: hsl(0, 0%, 96%);
}

/* Mark Cells */

tbody td {
  background-color: hsl(0, 0%, 11%); /*rgb(28, 28, 28);*/
}
tbody td.odd {
  background-color: hsl(34, 70%, 65%); /*burlywood;*/
}

/*
tbody td.off {
  
}
tbody td.odd.off {
  
}
*/

tbody td.editable {
  background-color: hsl(19, 70%, 40%); /*sienna;*/
}
tbody td.odd.editable {
  background-color: hsl(19, 70%, 50%);
}

tbody td.removable {
  background-color: hsl(0, 70%, 40%);
}
tbody td.odd.removable {
  background-color: hsl(0, 70%, 50%);
}

tbody td.transforming {
  background-color: hsl(300, 70%, 40%);
}
tbody td.odd.transforming {
  background-color: hsl(300, 70%, 50%);
}

tbody td.hovered-over:not(.removable.prior, .transforming.prior) {
  background-color: hsl(260, 70%, 40%);
}
tbody td.odd.hovered-over:not(.removable.prior, .transforming.prior) {
  background-color: hsl(260, 70%, 50%);
}

tbody td>div {
  width: 100%;
  height: 8vw; /* V */

  font-size: 0.9vw;
  font-family: 'Rock Salt', cursive;
  font-weight: bold;

  display: grid;
  grid-template-rows: 1.4vw 1fr 1.4vw;
  grid-template-columns: 1.4vw 1fr 1.4vw;
}

tbody td>div>div {
  grid-row: 2;
  grid-column: 2;

  display: flex;
  flex-direction: row;
  -webkit-flex-direction: row;
  justify-content: center;
  align-items: center;
}

tbody td>div>div>div {
  width: 100%;
  padding-top: 1.1vw;
  padding-bottom: 1.1vw;

  text-align: center;

  /* explicit double */
  color: hsl(0, 0%, 96%);
  box-shadow: 0px 0px 0.4vw 0 currentColor;
}

/* ? */
tbody td .stage-0 {
  display: none;
}

tbody td.active .stage-1 {
  background-color: hsl(200, 70%, 40%);
}
tbody td.active .stage-2 {
  background-color: hsl(280, 70%, 40%);
}
tbody td.active .stage-3 {
  background-color: hsl(300, 70%, 40%);
}

tbody td.passive>div>div>div {
  color: hsl(34, 70%, 65%);
}
tbody td.odd.passive>div>div>div {
  color: hsl(0, 0%, 11%);
}

tbody td.passive.editable>div>div>div {
  color: hsl(0, 0%, 71%); /*rgb(180, 180, 180);*/
  background-color: hsl(19, 70%, 40%);
}

tbody td.transforming:is(.active, .passive)>div>div>div {
  color: hsl(0, 0%, 96%);
  background-color: hsl(300, 70%, 40%);
}
tbody td.removable:is(.active, .passive)>div>div>div {
  color: hsl(0, 0%, 96%);
  background-color: hsl(0, 70%, 40%);
}
tbody td.hovered-over:not(.removable, .transforming)>div>div>div {
  color: hsl(0, 0%, 96%);
  background-color: hsl(260, 70%, 40%);
}

/* Transforming & Super mode */

tbody td:is(.transforming, .super)>div>div>div {
  background-size: 50%;
  background-repeat: no-repeat;
  background-position: center;
}

tbody td:is(.transforming, .super) .stage-1 {
  background-image: url('./icons/sheriff-star-light.svg');
}
tbody td.transforming .stage-2 {
  background-image: url('./icons/crown-dark.svg');
}
tbody td:is(.transforming, .super) .stage-3 {
  background-image: url('./icons/crown-light.svg');
}

tbody td:is(.transforming, .super)>div>div>div>span {
  visibility: hidden;
}

/* T-FOOT */

tfoot {
  width: 100%;
  height: 8vw;
}

tfoot th {
  width: 24vw; /* 30% */
}

/* T-FOOT BUTTONS - COMMON */

tfoot button {
  height: 5vw;

  border: 1px solid hsl(0, 0%, 96%);
  border-radius: 0.4vw;

  cursor: pointer;
}

tfoot button:hover {
  box-shadow: 0px 0px 0.4vw 0 hsl(0, 0%, 100%);
  transition: box-shadow 250ms ease-in-out 20ms;
}

tfoot button:active {
  background-color: hsl(0, 0%, 14%); /*rgb(42, 42, 42);*/
}

/* T-FOOT TH BUTTON */

tfoot th button {
  width: 100%;

  font-size: 1.2vw;
  font-family: 'Rock Salt', cursive;

  color: hsl(0, 0%, 96%);
  background-color: hsl(0, 0%, 11%);
}

/* T-FOOT TD BUTTONS */

tfoot td button {
  width: 16%;
  margin-left: 4%;

  font-size: 2vw;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

tfoot td button.active:nth-child(1) {
  background-color: hsl(200, 70%, 40%);
}
tfoot td button.active:nth-child(2) {
  background-color: hsl(280, 70%, 40%);
}
tfoot td button.active:nth-child(3) {
  background-color: hsl(300, 70%, 40%);
}
tfoot td button.active:nth-child(4) {
  background-image: -moz-linear-gradient(left, hsl(300, 70%, 40%), hsl(280, 70%, 40%));
  background-image: -webkit-gradient(linear, left center, right center, from(hsl(300, 70%, 40%)), to(hsl(280, 70%, 40%)));
}
tfoot td button.active:nth-child(5) {
  background-color: hsl(0, 0%, 11%);
}

tfoot td button.passive {
  background-color: hsl(34, 70%, 65%);
}

/* needs to be :nth-child(n) */
tfoot td button.active:nth-child(n):active {
  /* to remove bg gradient: */
  background-image: none;
  /* needs to be here: */
  background-color: hsl(0, 0%, 14%);
}

tfoot td button.passive:nth-child(n):active {
  background-color: hsl(34, 70%, 68%);
}

tfoot td button div {
  border-radius: 0.25vw;
  transition: color 750ms ease-in-out 200ms;
}

tfoot td button.active div {
  background-color: hsl(34, 70%, 65%);
}
tfoot td button.passive div {
  background-color: hsl(0, 0%, 11%);
}

tfoot td button.day-view div {
  color: hsl(19, 70%, 40%);
}
tfoot td button.activity-view div {
  color: hsl(25, 100%, 40%);
}
tfoot td button.activity-removable-view div {
  color: hsl(0, 70%, 40%);
}
tfoot td button.activity-transforming-view div {
  color: hsl(300, 70%, 40%);
}
tfoot td button.active.total-view div {
  color: hsl(0, 0%, 11%);
}
tfoot td button.passive.total-view div {
  color: hsl(34, 70%, 65%);
}

/* HEAD STRIPE (when position is fixed) */

.head-stripe {
  width: 100%;
  height: 5vw;
}

/* FOOT STRIPE (when position is fixed) */

.foot-stripe {
  width: 100%;
  height: 8vw;
}

/* EDIT BUTTONS - COMMON */

tbody button {
  margin: 10%;

  border-width: 0px;
  border-radius: 2px;

  cursor: pointer;

  background-size: 70%;
  background-repeat: no-repeat;
  background-position: center;

  color: inherit;
  box-shadow: 0px 0px 0.3vw 0 currentColor;
}

tbody button:active {
  transition: all 250ms ease-in-out 20ms;
}

/* ACTIVITY EDIT BUTTONS */

tbody th button {
  background-color: hsl(0, 0%, 14%);
}

tbody th.hovered-over button {
  background-color: hsl(39, 100%, 45%);
}

tbody th button:active {
  color: hsl(0, 0%, 0%);
}

tbody th.transforming button {
  background-color: hsl(300, 70%, 40%);
}
tbody th.transforming button:active {
  color: hsl(0, 0%, 96%);
  background-color: hsl(300, 70%, 50%);
}

tbody th.removable button {
  background-color: hsl(0, 70%, 40%);
}
tbody th.removable button:active {
  color: hsl(0, 0%, 96%);
  background-color: hsl(0, 70%, 50%);
}

tbody th.removable .up-button:hover {
  animation: button-shake 250ms ease-in-out 20ms;
}

@keyframes button-shake {
  from {
    background-image: none;
  }
  30% {
    transform: rotate(10deg);
  }
  70% {
    transform: rotate(-10deg);
  }
  to {
    background-image: none;
  }
}

tbody th .up-button {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 2;
}

tbody th .up-button {
  background-image: url('./icons/cross-orange.svg');
}
tbody th.hovered-over .up-button {
  background-image: url('./icons/cross-dark.svg');
}
tbody th:is(.removable, .transforming.super) .up-button {
  background-image: url('./icons/cross-light.svg');
}
tbody th.transforming .up-button {
  background-image: url('./icons/stop-light.svg');
}

tbody th .down-button {
  grid-column-start: 2;
  grid-column-end: 3;
  grid-row-start: 3;
  grid-row-end: 4;
}

tbody th .down-button {
  background-image: url('./icons/tick-orange.svg');
}
tbody th.hovered-over .down-button {
  background-image: url('./icons/tick-dark.svg');
}
tbody th:is(.removable, .transforming.super) .down-button {
  background-image: url('./icons/stop-light.svg');
}
tbody th.transforming .down-button {
  background-image: url('./icons/tick-light.svg');
}

/* MARK EDIT BUTTONS */

tbody td:not(.removable, .transforming, .hovered-over) button {
  color: hsl(0, 0%, 14%);
}

tbody td button {
  background-color: hsl(19, 70%, 40%);
}

tbody td.removable button {
  background-color: hsl(0, 70%, 40%);
}
tbody td.removable button:active {
  background-color: hsl(0, 70%, 50%);
}

tbody td.transforming button {
  background-color: hsl(300, 70%, 40%);
}
/*
tbody td.transforming button:active {
  background-color: hsl(300, 70%, 50%);
}
*/

tbody td.hovered-over:not(.removable.prior, .transforming.prior) button {
  background-color: hsl(260, 70%, 40%);
}
tbody td.hovered-over:not(.removable.prior, .transforming.prior) button:active {
  background-color: hsl(260, 70%, 50%);
}

tbody td.removable.super .up-button:hover {
  animation: button-shake 250ms ease-in-out 20ms;
}

tbody td .up-button {
  grid-row: 1;
  grid-column: 3;
}

tbody td .up-button {
  background-image: url('./icons/cross-dark.svg');
}
tbody td:is(.hovered-over, .removable, .transforming) .up-button {
  background-image: url('./icons/cross-light.svg');
}

tbody td .down-button {
  grid-row: 3;
  grid-column: 3;
}

tbody td .down-button {
  background-image: url('./icons/tick-dark.svg');
}
tbody td:is(.hovered-over, .removable, .transforming) .down-button {
  background-image: url('./icons/tick-light.svg');
}
tbody td.removable.super .down-button {
  background-image: url('./icons/stop-light.svg');
}

/* BOTTOM AREA */

#bottom-area {
  position: fixed;
  bottom: 0px;
  
  width: fit-content;
  margin: 0 10vw;

  z-index: -7;

  font-size: calc(12px + 0.2vw);
  font-family: Verdana, Geneva, Tahoma, sans-serif;

  color: hsl(0, 0%, 96%);
}

#service-panel {
  padding: 10px 0px;

  /*visibility: hidden;*/
}

#bottom-area>div:nth-child(2) {
  margin: 10px 0px;
}

#bottom-area a {
  color: inherit;
}

#aux-area {
  width: 24vw;
  margin-top: 10px;
  
  border: 1px solid currentColor;
  border-radius: 0.4vw;
}

#aux-area>div {
  height: 5vw;
}

/* MEDIA */

/* 0.6 FULL-HD screen width */
@media screen and (max-width: 1152px) {

  thead th span:nth-child(2) {
    text-transform: uppercase;
  }
  thead th span:nth-child(3) {
    display: none;
  }
  thead th:not(.free, .done, .super, .hovered-over):last-child span:first-child {
    color: hsl(39, 100%, 45%);
  }

  table {
    width: 90vw;
    margin: 0 5vw;
  }
  #bottom-area {
    margin: 0 5vw;
  }
  thead, .head-stripe {
    height: 5.625vw;
  }
  thead th:first-child, tbody th, tfoot th, #aux-area {
    width: 27vw; /* 30% */
  }
  tbody th>div, tbody td>div {
    height: 9vw; /* V */
  }
  tfoot, .foot-stripe {
    height: 9vw;
  }
  tfoot button, #aux-area>div {
    height: 5.625vw;
  }

  /* * */

  thead, tfoot th button {
    font-size: 2.025vw;
  }

  tbody th>div>div>div {
    width: 21.375vw;
    margin-left: 2.25vw;
    /*margin-right: 2.25vw;*/
  }
  
  tbody th>div {
    grid-template-rows: 1.575vw 1fr 1.575vw;
    grid-template-columns: 1fr 1.575vw;
  }
  tbody td>div {
    grid-template-rows: 1.575vw 1fr 1.575vw;
    grid-template-columns: 1.575vw 1fr 1.575vw;

    font-size: 1.0125vw;
  }

  tbody td>div>div>div {
    padding-top: 1.2375vw;
    padding-bottom: 1.2375vw;

    box-shadow: 0px 0px 0.45vw 0 currentColor;
  }

  tfoot td button {
    font-size: 2.25vw;
  }

  tfoot button, #aux-area {
    border-radius: 0.45vw;
  }
  tfoot td button div {
    border-radius: 0.3375vw;
  }

  tbody button {
    box-shadow: 0px 0px 0.3375vw 0 currentColor;
  }
  tfoot button:hover {
    box-shadow: 0px 0px 0.45vw 0 hsl(0, 0%, 100%);
  }

  tbody th {
    line-height: 1.8vw;
  }
  tbody th, #aux-area {
    font-size: 1.35vw;
  }
  textarea {
    width: 21.375vw;
    margin-left: 2.25vw;

    line-height: 1.8vw;
  }
}

/* STOPPING DIMENSIONS */
@media screen and (max-width: 889px) {

  .app {
    width: 889px;
  }

  table {
    width: 800px;
    margin: 0 45px;
  }
  #bottom-area {
    margin: 0 45px;
  }
  thead, .head-stripe {
    height: 50px;
  }
  thead th:first-child, tbody th, tfoot th, #aux-area {
    width: 240px; /* 30% */
  }
  tbody th>div, tbody td>div {
    height: 80px; /* V */
  }
  tfoot, .foot-stripe {
    height: 80px;
  }
  tfoot button, #aux-area>div {
    height: 50px;
  }

  /* * */

  thead, tfoot th button {
    font-size: 18px;
  }
  
  tbody th>div>div>div {
    width: 190px;
    margin-left: 20px;
    /*margin-right: 20px;*/
  }
  
  tbody th>div {
    grid-template-rows: 14px 1fr 14px;
    grid-template-columns: 1fr 14px;
  }
  tbody td>div {
    grid-template-rows: 14px 1fr 14px;
    grid-template-columns: 14px 1fr 14px;

    font-size: 9px;
  }

  tbody td>div>div>div {
    padding-top: 11px;
    padding-bottom: 11px;

    box-shadow: 0px 0px 4px 0 currentColor;
  }

  tfoot td button {
    font-size: 20px;
  }

  tfoot button, #aux-area {
    border-radius: 4px;
  }
  tfoot td button div {
    border-radius: 3px;
  }

  tbody button {
    box-shadow: 0px 0px 3px 0 currentColor;
  }
  tfoot button:hover {
    box-shadow: 0px 0px 4px 0 hsl(0, 0%, 100%);
  }

  tbody th {
    line-height: 16px;
  }
  tbody th, #aux-area {
    font-size: 12px;
  }
  textarea {
    width: 190px;
    margin-left: 20px;

    line-height: 16px;
  }
}